.box {
    .slogan {
        .logo {
            height: 2.5rem;
            width: auto;
        }

        .slogan-video {
            width: 60%;
            height: auto;
            border-radius: 10px;
        }

        .url {
            text-decoration: none;
            color: rgb(94, 88, 231);

            &:hover {
                color: rgb(49, 41, 234);
            }
        }
    }

    .goTopBtn {
        position: fixed !important;
        bottom: 1rem;
        right: 1rem;
        background-color: rgb(94, 88, 231);

        &:hover {
            background-color: rgb(49, 41, 234);
        }
    }
}